import React from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { useCreatorContext } from '../context/ContextProvider'; // Import JobProvider

const NavBar = () => {

  const { data, selectRandomJob } = useCreatorContext();

  // Function to handle random selection on button click
  const handleRandomSelection = () => {
    if (data) {
      selectRandomJob(data); 
    }
  };

  return (
    <AppBar position="sticky" sx={{ backgroundColor: "#333" }}>
      <Toolbar>
        {/* Navbar Title */}
        <Typography variant="h6" sx={{ flexGrow: 1 }}>
          Hello World!
        </Typography>

        {/* Shuffle Button */}
        <Button
          variant="contained"
          color="primary"
          onClick={handleRandomSelection} 
          sx={{
            marginLeft: "auto", 
          }}
        >
          Shuffle Topic
        </Button>
      </Toolbar>
    </AppBar>
  );
};

export default NavBar;