import React, { useEffect, useState } from 'react';
import { useCreatorContext } from '../context/ContextProvider';
import { TransformWrapper, TransformComponent } from 'react-zoom-pan-pinch';
import {
    Box,
    Tooltip,
    Typography
  } from "@mui/material";
  const getClusterColor = (clusterId) => {
    // if (clusterId == "-1") {
    //     return "#000000";
    // }
    const colors = [
      "#FF5733", "#33FF57", "#3357FF", "#F833FF", "#FF5733", 
      "#57FF33", "#FF33A1", "#33FFF5", "#FF8C33", "#33FF8C", 
      "#F5FF33", "#8C33FF", "#FF338C", "#33A1FF", "#8CFF33", 
      "#A133FF", "#33FF33", "#8C8CFF", "#FF3388", "#57A1FF"
    ];
    return colors[(clusterId) % colors.length]
  };

const GroupMap = () => {
  const { data, selectSpecificJob } = useCreatorContext();
  const [clusters, setClusters] = useState([]);
  
  useEffect(() => {
    if (data) {
        const clusterList = Object.keys(data).map((clusterId) => {
            return {
              clusterId,
              items: data[clusterId].items.map((item) => ({
                ...item,
                color: getClusterColor(clusterId),
                clusterId: clusterId
              })),
            };
        });
        setClusters(clusterList);

    }
    
  }, [data]);

  if (!clusters || clusters.length == 0) {
    return <p>Loading...</p>;
  }

  
  const handlePointClick = (clusterId) => {
    selectSpecificJob(data, clusterId)
    window.scrollTo(0, 0);
  };

  return (
    <>
    <Typography variant="h5" gutterBottom>
        Mind Map
    </Typography>


    <div style={{ width: '100%', height: '100vh', border: '2px solid #000', padding: '10px' }}>
      <TransformWrapper
      >
        <TransformComponent>

            
          <svg width="800px" height="800px">
          {clusters.map((cluster, clusterIndex) =>
              cluster.items.map((item, index) => (
                <Tooltip key={`${clusterIndex}-${index}`} title={item.llm_title} placement="top">
                  <circle
                    cx={(item.location[0]+60)*5} // Center and scale the X axis
                    cy={(item.location[1]+60)*5} // Center and scale the Y axis
                    r="5"
                    fill={item.color}
                    onClick={() => handlePointClick(item.clusterId)}
                    style={{ cursor: 'pointer' }}
                  />
                </Tooltip>
              ))
            )}
          </svg>
        </TransformComponent>
      </TransformWrapper>
    </div>
    </>
  )
};

export default GroupMap;
